<template>
<div>
  <H1>雅居乐花园小区"四返"人员信息统计</H1>
  <p class="text">1.两省四市返回人员共{{hotRooms}}户,{{hotPeople}}人.</p>
  <p class="text">(目前正在隔离{{hotIsolateRooms}}户{{hotIsolatePeople}}人.其中湖北{{hubeiIsolateRooms}}户{{hubeiIsolatePeople}}人.)</p>
  <p class="text">2.其他四返人员共{{otherRooms}}户{{otherPeople}}人.</p>
  <p class="text">(目前自行隔离{{otherIsolateRooms}}户{{otherIsolatePeople}}人,解除隔离{{freeOtherRooms}}户{{freeOtherPeople}}人.)</p>
  <p class="text">3.网格化管理人员(市码扫码人员)共{{qrcodeRooms}}户{{qrcodePeople}}人.</p>
  <p class="text">(目前扫码隔离{{qrcodeIsolateRooms}}户{{qrcodeIsolatePeople}}人,解除{{qrcodeFreeRooms}}户{{qrcodeFreePeople}}人.)</p>
</div>
</template>

<script>
    export default {
        name: "Statistics",
      data () {
        return {

          hotRooms:null,
          hotPeople:null,
          hotIsolateRooms:null,
          hotIsolatePeople:null,
          hubeiIsolateRooms:null,
          hubeiIsolatePeople:null,
          otherRooms:null,
          otherPeople:null,
          otherIsolateRooms:null,
          otherIsolatePeople:null,
          freeOtherRooms:null,
          freeOtherPeople:null,
          qrcodeRooms:null,
          qrcodePeople:null,
          qrcodeIsolateRooms:null,
          qrcodeIsolatePeople:null,
          qrcodeFreeRooms:null,
          qrcodeFreePeople:null

        }
      }
      ,
      methods: {
        loadData () {

          var that = this;

          this.$axios.get('https://www.geckos.top/api/statistics')
            .then(res => {

              if (res.data!=null) {

                var stat = res.data;

                  that.hotRooms=stat.hotRooms;
                  that.hotPeople=stat.hotPeople;
                  that.hotIsolateRooms=stat.hotIsolateRooms;
                  that.hotIsolatePeople=stat.hotIsolatePeople;
                  that.hubeiIsolateRooms=stat.hubeiIsolateRooms;
                  that.hubeiIsolatePeople=stat.hubeiIsolatePeople;
                  that.otherRooms=stat.otherRooms;
                  that.otherPeople=stat.otherPeople;
                  that.otherIsolateRooms=stat.otherIsolateRooms;
                  that.otherIsolatePeople=stat.otherIsolatePeople;
                  that.freeOtherRooms=stat.freeOtherRooms;
                  that.freeOtherPeople=stat.freeOtherPeople;
                  that.qrcodeRooms=stat.qrcodeRooms;
                  that.qrcodePeople=stat.qrcodePeople;
                  that.qrcodeIsolateRooms=stat.qrcodeIsolateRooms;
                  that.qrcodeIsolatePeople=stat.qrcodeIsolatePeople;
                  that.qrcodeFreeRooms=stat.qrcodeFreeRooms;
                  that.qrcodeFreePeople=stat.qrcodeFreePeople;


              }

        })
        }
      },
      created () {
        this.loadData()
      },
      mounted () {

      }
    }
</script>

<style scoped>
  .text{
    font-size:1.2em;
    font-weight: bold;
    text-align: left;
  }

</style>



